:root {
  --font-body: "Lato", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --brown: hsl(29, 85%, 35%);
  --brown-light:hsla(29, 85%, 35%, 0.17);
  --brown-lighter:hsla(29, 85%, 35%, 0.37);
  --brown-dark:hsla(30, 77%, 36%, 1);
  --brown-darker: hsla(29, 84%, 25%, 1);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
}

.title {
  font-weight: 900;
  font-size: 2.25rem;
  text-align: center;
}

.paragraph {
  font-weight: 400;
  font-size: 0.875rem;
}

.container {
  padding: 0 8.75rem;
  max-width: 90rem;
  margin: 0 auto;
}
@media (max-width: 59.375em) {
  .container {
    padding: 0 6.25rem;
  }
}
@media (max-width: 39.9375em) {
  .container {
    padding: 0 2.5rem;
  }
}

.nous_contacter .contact {
  justify-content: center;
}
@media (max-width: 39.9375em) {
  .nous_contacter .contact {
    grid-template-columns: 1fr;
    gap: 1.0625rem;
  }
}
.nous_contacter .contact-img .title {
  display: none;
}
@media (max-width: 39.9375em) {
  .nous_contacter .contact-img .title {
    display: block;
    color: var(--brown);
  }
  .nous_contacter .contact-img .title b {
    font-weight: 300;
  }
}
.nous_contacter .contact-img img {
  width: 100%;
  animation: float 3s linear infinite;
}
@keyframes float {
  0%, 100% {
    transform: translateY(0rem);
  }
  50% {
    transform: translateY(2rem);
  }
}
.nous_contacter .contact-text {
  display: flex;
  flex-direction: column;
  align-self: center;
}
.nous_contacter .contact-text .title {
  color: var(--brown);
}
@media (max-width: 59.375em) {
  .nous_contacter .contact-text .title {
    font-size: 1.875rem;
  }
}
@media (max-width: 39.9375em) {
  .nous_contacter .contact-text .title {
    display: none;
  }
}
.nous_contacter .contact-text .title b {
  font-weight: 300;
}
.nous_contacter .contact-text .paragraph {
  margin: revert;
}
.nous_contacter .contact-text .btnbg {
  align-self: center;
}

.grid-2--cols {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.grid-3--cols {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.footer {
  background-color: var(--brown-dark);
}
@media (max-width: 59.375em) {
  .footer {
    text-align: center;
  }
}
.footer-bloc {
  font-weight: 300;
  color: var(--white);
  padding-top: 4.5rem;
  padding-bottom: 5.5rem;
}
@media (max-width: 59.375em) {
  .footer-bloc {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 39.9375em) {
  .footer-bloc {
    grid-template-columns: repeat(1, 1fr);
  }
}
.footer-bloc .paragraph {
  margin: revert;
  font-weight: 300;
}
.footer-bloc .title {
  text-align: inherit;
}
.footer-copy {
  background-color: var(--brown-darker);
  color: var(--white);
  padding-top: 0.9375rem;
  padding-bottom: 0.9375rem;
}
.footer-copy span {
  font-weight: 300;
}

.btnbg {
  font-weight: 700;
  font-size: 0.8125rem;
  color: var(--white);
  padding: 0.5125rem 1.5625rem 0.5125rem 1.5625rem;
  border-radius: 0.5625rem;
  background-color: var(--brown);
}

.btnline {
  display: inline-flex;
  border-bottom: 0.05rem solid var(--brown);
  font-weight: 400;
  font-size: 1.125rem;
}